import { _decorator, Color, Node, Sprite, Vec3 } from "cc";
import { EffectManager } from "../Effects/EffectManager";
import { PoolHelper } from "../PoolHelper";
import { PiecesPool } from "../Poolable/PiecesPool";
const { ccclass } = _decorator;

enum Direction {
  Horizontal,
  Vertical,
}

@ccclass("MatchChecker")
export class MatchChecker {
  private static horizontalDirections = [
    { dx: 0, dy: 1 }, // Right
    { dx: 0, dy: -1 }, // Left
  ];

  private static verticalDirections = [
    { dx: 1, dy: 0 }, // Down
    { dx: -1, dy: 0 }, // Up
  ];

  public static async SwapPiecePositions(
    pieceA: Node,
    pieceB: Node,
    posA: Vec3,
    posB: Vec3,
    gridMap: Map<Node, { row: number; col: number }>
  ) {
    EffectManager.movePiece(pieceA, posB, 0.1);
    await EffectManager.movePiece(pieceB, posA, 0.1);

    // Update the gridMap
    const posAData = gridMap.get(pieceA);
    const posBData = gridMap.get(pieceB);
    gridMap.set(pieceA, posBData);
    gridMap.set(pieceB, posAData);

    this.checkMatches(pieceB, gridMap);
    this.checkMatches(pieceA, gridMap);

  }

  private static getPieceAtPosition(
    position: { row: number; col: number },
    gridMap: Map<Node, { row: number; col: number }>
  ): Node | undefined {
    return Array.from(gridMap.entries()).find(
      ([_, pos]) => pos.row === position.row && pos.col === position.col
    )?.[0];
  }

  private static visitNeighborWithDirection(
    currentPiece: Node,
    gridMap: Map<Node, { row: number; col: number }>,
    direction: { dx: number; dy: number },
    visited: Set<Node>,
    depth: number,
    maxDepth: number
  ): void {
    if (depth > maxDepth) return;

    const currentPiecePosition = gridMap.get(currentPiece);
    const neighborPiecePosition = {
      row: currentPiecePosition.row + direction.dx,
      col: currentPiecePosition.col + direction.dy,
    };

    const neighbor = this.getPieceAtPosition(neighborPiecePosition, gridMap);
    if (
      neighbor &&
      !visited.has(neighbor) &&
      neighbor.name === currentPiece.name
    ) {
      const sprite = neighbor.getChildByName("Sprite").getComponent(Sprite);
      if (sprite) {
        sprite.color = new Color(150, 150, 255); // Change color
      }
      visited.add(neighbor);
      this.visitNeighborWithDirection(
        neighbor,
        gridMap,
        direction,
        visited,
        depth + 1,
        maxDepth
      );
    }
  }

  private static async checkMatches(
    currentPiece: Node,
    gridMap: Map<Node, { row: number; col: number }>,
    depth: number = 0,
    maxDepth: number = 3
  ) {
    const horizontalVisited = new Set<Node>();
    const verticalVisited = new Set<Node>();


    horizontalVisited.add(currentPiece);
    verticalVisited.add(currentPiece);

    for (const direction of this.horizontalDirections) {
      this.visitNeighborWithDirection(
        currentPiece,
        gridMap,
        direction,
        horizontalVisited,
        depth,
        maxDepth
      );
    }
    for (const direction of this.verticalDirections) {
      this.visitNeighborWithDirection(
        currentPiece,
        gridMap,
        direction,
        verticalVisited,
        depth,
        maxDepth
      );
    }

    if (verticalVisited.size >= 3) {
      console.log("Vertical Match Detected");
      await this.handleMatch(Array.from(verticalVisited), gridMap);
    } 
    if (horizontalVisited.size >= 3) {
      console.log("Horizontal Match Detected");
      await this.handleMatch(Array.from(horizontalVisited), gridMap);
    }
  }

  private static async handleMatch(matchedPieces: Node[], gridMap: Map<Node, { row: number; col: number }>): Promise<void> {
    for (const piece of matchedPieces) {
      EffectManager.decreaseScale(piece , 0.1 , false);
      gridMap.delete(piece);
    }
  }

}
